import React, { useState, useEffect, useRef } from 'react'
import { Button } from './ui/button'
import { Input } from './ui/input'
import { Card, CardContent, CardHeader, CardTitle } from './ui/card'
import { Avatar, AvatarFallback } from './ui/avatar'
import VoiceRecognition from './VoiceRecognition'
import ChatMessage from './ChatMessage'
import { generateElderlyFriendlyResponse, speakText, getFriendlyVoice } from '../lib/utils'
import { Send, Heart, Settings, Volume2, VolumeX, Sun, Moon } from 'lucide-react'
import { motion, AnimatePresence } from 'framer-motion'

const ChatInterface = () => {
  const [messages, setMessages] = useState([])
  const [inputValue, setInputValue] = useState('')
  const [isListening, setIsListening] = useState(false)
  const [isSpeaking, setIsSpeaking] = useState(true)
  const [isTyping, setIsTyping] = useState(false)
  const [error, setError] = useState(null)
  const [darkMode, setDarkMode] = useState(false)
  const [showVoicePanel, setShowVoicePanel] = useState(false)
  const messagesEndRef = useRef(null)
  const inputRef = useRef(null)

  // Initialize with welcome message
  useEffect(() => {
    const welcomeMessage = {
      id: 1,
      content: "Hello! I'm your friendly AI companion. I'm here to chat with you, answer your questions, and keep you company. You can type your messages or use your voice to talk with me. How are you feeling today?",
      timestamp: new Date(),
      isUser: false
    }
    setMessages([welcomeMessage])
  }, [])

  // Auto-scroll to bottom when new messages arrive
  useEffect(() => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' })
  }, [messages])

  // Handle voice transcript
  const handleVoiceTranscript = (transcript) => {
    if (transcript.trim()) {
      handleSendMessage(transcript)
    }
  }

  // Handle voice errors
  const handleVoiceError = (error) => {
    setError(error)
    setTimeout(() => setError(null), 5000)
  }

  // Send message function
  const handleSendMessage = async (content) => {
    if (!content.trim()) return

    const userMessage = {
      id: Date.now(),
      content: content.trim(),
      timestamp: new Date(),
      isUser: true
    }

    setMessages(prev => [...prev, userMessage])
    setInputValue('')
    setIsTyping(true)

    // Simulate AI response
    setTimeout(() => {
      const aiResponse = {
        id: Date.now() + 1,
        content: generateElderlyFriendlyResponse(content),
        timestamp: new Date(),
        isUser: false
      }
      setMessages(prev => [...prev, aiResponse])
      setIsTyping(false)
    }, 1000 + Math.random() * 2000) // Random delay for natural feel
  }

  // Handle form submission
  const handleSubmit = (e) => {
    e.preventDefault()
    handleSendMessage(inputValue)
  }

  // Handle speak function
  const handleSpeak = (text) => {
    if (isSpeaking) {
      speakText(text, getFriendlyVoice())
    }
  }

  // Toggle dark mode
  const toggleDarkMode = () => {
    setDarkMode(!darkMode)
    document.documentElement.classList.toggle('dark')
  }

  // Toggle voice panel
  const toggleVoicePanel = () => {
    setShowVoicePanel(!showVoicePanel)
  }

  return (
    <div className={`min-h-screen transition-colors duration-300 ${darkMode ? 'dark bg-gray-900' : 'bg-gradient-to-br from-blue-50 to-indigo-100'}`}>
      <div className="container mx-auto px-4 py-6 max-w-6xl">
        {/* Header */}
        <motion.div
          initial={{ opacity: 0, y: -20 }}
          animate={{ opacity: 1, y: 0 }}
          className="flex items-center justify-between mb-8"
        >
          <div className="flex items-center space-x-4">
            <Avatar className="h-16 w-16">
              <AvatarFallback className="bg-elder-green text-white text-2xl">
                AI
              </AvatarFallback>
            </Avatar>
            <div>
              <h1 className="text-3xl font-bold text-gray-800 dark:text-white">
                Friendly Chatbot
              </h1>
              <p className="text-lg text-gray-600 dark:text-gray-300">
                Your AI companion for conversation and support
              </p>
            </div>
          </div>
          
          <div className="flex items-center space-x-3">
            <Button
              onClick={toggleVoicePanel}
              variant="elderlySecondary"
              size="lg"
              aria-label="Toggle voice controls"
            >
              {showVoicePanel ? <VolumeX className="h-6 w-6" /> : <Volume2 className="h-6 w-6" />}
            </Button>
            
            <Button
              onClick={toggleDarkMode}
              variant="elderlySecondary"
              size="lg"
              aria-label="Toggle dark mode"
            >
              {darkMode ? <Sun className="h-6 w-6" /> : <Moon className="h-6 w-6" />}
            </Button>
          </div>
        </motion.div>

        {/* Voice Panel */}
        <AnimatePresence>
          {showVoicePanel && (
            <motion.div
              initial={{ opacity: 0, height: 0 }}
              animate={{ opacity: 1, height: 'auto' }}
              exit={{ opacity: 0, height: 0 }}
              transition={{ duration: 0.3 }}
              className="mb-6"
            >
              <Card>
                <CardContent className="p-0">
                  <VoiceRecognition
                    onTranscript={handleVoiceTranscript}
                    onError={handleVoiceError}
                    isListening={isListening}
                    setIsListening={setIsListening}
                    isSpeaking={isSpeaking}
                    setIsSpeaking={setIsSpeaking}
                  />
                </CardContent>
              </Card>
            </motion.div>
          )}
        </AnimatePresence>

        {/* Error Message */}
        <AnimatePresence>
          {error && (
            <motion.div
              initial={{ opacity: 0, y: -10 }}
              animate={{ opacity: 1, y: 0 }}
              exit={{ opacity: 0, y: -10 }}
              className="mb-4 p-4 bg-red-100 border border-red-300 rounded-xl text-red-700"
            >
              <p className="text-lg">{error}</p>
            </motion.div>
          )}
        </AnimatePresence>

        {/* Chat Messages */}
        <Card className="mb-6 h-96 overflow-hidden">
          <CardHeader className="bg-gradient-to-r from-elder-blue to-blue-600 text-white">
            <CardTitle className="text-xl">Conversation</CardTitle>
          </CardHeader>
          <CardContent className="p-0 h-full">
            <div className="h-full overflow-y-auto p-6 space-y-4">
              <AnimatePresence>
                {messages.map((message) => (
                  <ChatMessage
                    key={message.id}
                    message={message}
                    isUser={message.isUser}
                    isSpeaking={isSpeaking}
                    onSpeak={handleSpeak}
                    voiceEnabled={isSpeaking}
                  />
                ))}
              </AnimatePresence>
              
              {/* Typing indicator */}
              <AnimatePresence>
                {isTyping && (
                  <motion.div
                    initial={{ opacity: 0, y: 20 }}
                    animate={{ opacity: 1, y: 0 }}
                    exit={{ opacity: 0, y: 20 }}
                    className="flex justify-start mb-6"
                  >
                    <div className="flex items-start space-x-3">
                      <Avatar className="flex-shrink-0">
                        <AvatarFallback className="bg-elder-green">
                          AI
                        </AvatarFallback>
                      </Avatar>
                      <Card className="bg-white border-2 border-gray-200">
                        <CardContent className="p-4">
                          <div className="flex space-x-1">
                            <div className="w-2 h-2 bg-gray-400 rounded-full animate-bounce-gentle"></div>
                            <div className="w-2 h-2 bg-gray-400 rounded-full animate-bounce-gentle" style={{ animationDelay: '0.1s' }}></div>
                            <div className="w-2 h-2 bg-gray-400 rounded-full animate-bounce-gentle" style={{ animationDelay: '0.2s' }}></div>
                          </div>
                        </CardContent>
                      </Card>
                    </div>
                  </motion.div>
                )}
              </AnimatePresence>
              
              <div ref={messagesEndRef} />
            </div>
          </CardContent>
        </Card>

        {/* Input Form */}
        <Card>
          <CardContent className="p-6">
            <form onSubmit={handleSubmit} className="flex space-x-4">
              <Input
                ref={inputRef}
                value={inputValue}
                onChange={(e) => setInputValue(e.target.value)}
                placeholder="Type your message here..."
                className="flex-1 text-lg"
                disabled={isListening}
              />
              <Button
                type="submit"
                variant="elderly"
                size="lg"
                disabled={!inputValue.trim() || isListening}
                aria-label="Send message"
              >
                <Send className="h-6 w-6" />
              </Button>
            </form>
            
            {/* Quick Actions */}
            <div className="mt-4 flex flex-wrap gap-3">
              <Button
                variant="elderlySecondary"
                size="sm"
                onClick={() => handleSendMessage("How are you today?")}
                disabled={isListening}
              >
                How are you?
              </Button>
              <Button
                variant="elderlySecondary"
                size="sm"
                onClick={() => handleSendMessage("Tell me a story")}
                disabled={isListening}
              >
                Tell me a story
              </Button>
              <Button
                variant="elderlySecondary"
                size="sm"
                onClick={() => handleSendMessage("What's the weather like?")}
                disabled={isListening}
              >
                Weather
              </Button>
              <Button
                variant="elderlySecondary"
                size="sm"
                onClick={() => handleSendMessage("I need some company")}
                disabled={isListening}
              >
                I need company
              </Button>
            </div>
          </CardContent>
        </Card>

        {/* Footer */}
        <motion.div
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          transition={{ delay: 0.5 }}
          className="mt-8 text-center"
        >
          <p className="text-gray-600 dark:text-gray-400 text-lg">
            Made with <Heart className="inline h-5 w-5 text-red-500" /> for elderly users
          </p>
          <p className="text-gray-500 dark:text-gray-500 text-base mt-2">
            Accessible • Voice-enabled • User-friendly
          </p>
        </motion.div>
      </div>
    </div>
  )
}

export default ChatInterface 