import * as React from "react"
import { Slot } from "@radix-ui/react-slot"
import { cva } from "class-variance-authority"
import { cn } from "../../lib/utils"

const buttonVariants = cva(
  "inline-flex items-center justify-center whitespace-nowrap rounded-xl text-lg font-semibold ring-offset-background transition-all duration-200 ease-in-out focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-ring focus-visible:ring-offset-2 disabled:pointer-events-none disabled:opacity-50 active:scale-95",
  {
    variants: {
      variant: {
        default: "bg-primary text-primary-foreground hover:bg-primary/90",
        destructive:
          "bg-destructive text-destructive-foreground hover:bg-destructive/90",
        outline:
          "border border-input bg-background hover:bg-accent hover:text-accent-foreground",
        secondary:
          "bg-secondary text-secondary-foreground hover:bg-secondary/80",
        ghost: "hover:bg-accent hover:text-accent-foreground",
        link: "text-primary underline-offset-4 hover:underline",
        elderly: "bg-elder-blue text-white hover:bg-blue-700 hover:shadow-lg px-6 py-4",
        elderlySecondary: "bg-gray-100 text-gray-800 hover:bg-gray-200 hover:shadow-md px-6 py-4",
        voice: "bg-elder-green text-white hover:bg-green-700 hover:shadow-lg px-6 py-4 rounded-full",
        voiceActive: "bg-elder-orange text-white hover:bg-orange-700 hover:shadow-lg px-6 py-4 rounded-full animate-pulse-gentle",
      },
      size: {
        default: "h-14 px-6 py-4",
        sm: "h-12 px-4 py-3",
        lg: "h-16 px-8 py-5 text-xl",
        xl: "h-20 px-10 py-6 text-2xl",
        icon: "h-14 w-14",
        voiceIcon: "h-20 w-20 rounded-full",
      },
    },
    defaultVariants: {
      variant: "default",
      size: "default",
    },
  }
)

const Button = React.forwardRef(({ className, variant, size, asChild = false, ...props }, ref) => {
  const Comp = asChild ? Slot : "button"
  return (
    <Comp
      className={cn(buttonVariants({ variant, size, className }))}
      ref={ref}
      {...props}
    />
  )
})
Button.displayName = "Button"

export { Button, buttonVariants } 